@media (min-width: 768px) {
  .container {
    width: 750px;
  }
}

@media (min-width: 992px) {
  .container {
    width: 970px;
  }
}

@media (min-width: 1200px) {
  .container {
    width: 1170px;
  }
}

.container {
  padding-right: 15px;
  padding-left: 15px;
  margin-right: auto;
  margin-left: auto;
}

.section {
  background-color: #FFF;
  padding-top: 60px;
  padding-bottom: 50px;
}

.logo {
  width: 95px;
  height: 31px;
  margin: 16px 24px 16px 0;
  float: left;

  img {
    max-width: 100%;
  }
}

.UserAvatar {
  display: flex;
  justify-content: center;
  align-items: center;

  .UserAvatar__Name {
    margin-left: 10px;
  }
}



